header{display:flex;justify-content:center;align-items:center;position:fixed;z-index:1000;top:0;width:100%;height:72px;border-bottom:1px solid var(--colorBg1);background-color:#fff;font-family:Apple SD Gothic Neo,Malgun Gothic,맑은 고딕,sans-serif}header a{color:#000;text-decoration:none}header .wrap-header{position:relative;width:100%;height:100%;max-width:1080px;margin:0 8px;display:flex;justify-content:center;align-items:center}header .wrap-header h1{position:absolute;left:0;padding:0;margin:0;letter-spacing:2px;transform:skew(-10deg);text-shadow:2px 2px 0 #3a5bff}header .wrap-header nav{display:flex;align-items:center;height:100%}header .wrap-header nav ul{height:100%;display:flex;align-items:center;align-content:center;list-style:none;padding:0;margin:0;color:#000}header .wrap-header nav ul li{display:flex;align-items:center;font-weight:700}header .wrap-header nav ul li a{padding:0 28px;line-height:72px;text-decoration:none}@media(max-width: 700px){header{height:auto}header .wrap-header{flex-direction:column}header .wrap-header h1{position:static}header .wrap-header nav>ul>li>a{padding:8px 28px;line-height:normal}}@media(min-width: 701px){nav>ul:hover a{transition:all .2s ease-out;color:var(--color1)}nav>ul:hover a:hover{color:#000;background-color:var(--color2)}}